@media only screen and (min-width: 600px) {
    .profile__header {
        display: flex;
        position: sticky;
        top: 0;
        background-color: black;
        z-index: 3;
        padding: .5rem 0 .5rem 0;
        background-color: rgba(0, 0, 0, 0.8);
    }
    
    @supports ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
        .profile__header {
            background: transparent;
            backdrop-filter: blur(15px);
        }
      }
    
    .profile__header__back {
        display: flex;
        align-items: center;
        margin-right: 2rem;
    }
    
    .profile__header__back > button {
        display: flex;
        padding: .5rem;
        margin: 0 .5rem 0 .5rem;
        background-color: transparent;
        border: none;
    }
    
    .profile__header__back > button:hover {
        background-color: #272a2e;
        border-radius: 2rem;
    }
    
    .profile__header__main {
        display: flex;
        flex-direction: column;
    }
    
    .profile__header__main > h3 {
        margin: 0 0 0 0;
    }
    
    .profile__header__main > span {
        font-size: smaller;
    }
    
    
    .profile__banner {
        display: flex;
        height: 11rem;
        border: none;
        width: 100%;
        background-color: rgb(47, 51, 54);
        z-index: 1;
        object-fit: fill;
    }
    
    .profile__picture {
        position: absolute;
        transform: translateX(20%) translateY(-50%);
        max-height: 120px;
        max-width: 120px;
        min-height: 120px;
        background-color: #bbb;
        border-radius: 50%;
        z-index: 2;
        overflow: visible;
        border: 5px solid black;
    }
    
    .profile__details {
        margin-bottom: 1rem;
        margin-left: 1rem;
        margin-right: 1rem;
    }
    
    .profile__follow {
        margin-top: 1rem;
    }
    
    
    .profile__follow > a {
        text-decoration: none;
        color: white;
        font-size: smaller;
        margin-right: 1rem;
    }
    
    .profile__follow > a:hover {
        text-decoration: 1px underline;
    }
    
    .profile__span {
        color: rgb(101, 119, 134);
        margin-top: .25rem;
    }
    
    #user {
        font-size: 1.5rem;
    }
    
    .profile__list {
        list-style: none;
        max-height: 60vh;
        overflow-y: auto;
        overflow-x: hidden;
    }
    
    .profile__item {
        margin: 0;
        padding: 0;
        display: flex;
        justify-content: space-between;
        min-width: 0;
    }
    
    .profile__item:hover {
        background-color: #272a2e;
    }
    
    .profile__item > a {
        display: flex;
        flex-grow: 1;
        padding: 1rem;
        text-decoration: none;
        color: white;
        align-items: center;
        justify-content: space-between;
        min-width: 0;
    }
    
    .profile__user {
        display: flex;
        align-items: center;
        max-width: 60%;
        
    }

    .profile__user > span {
        font-size: 20px;
        overflow: hidden;
    }
    
    .profile__user__icon {
        height: 3rem;
        margin-right: 1rem;
        border-radius: 5rem;
        max-width: 50px;
        min-width: 50px;
        max-height: 50px;
        min-height: 50px;
    }
    
    .profile__user {
        font-size: large;
    }
    
    .profile__user:hover {
        text-decoration: underline;
    }
    
    .profile__tabs {
        display: flex;
        justify-content: space-around;
        border-bottom: 1px solid rgb(95, 108, 116);
        height: 4rem;
    }
    
    .profile__tabs a {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        text-decoration: none;
        align-items: center;
        justify-content: center;
    }
    
    .profile__tabs a > span {
        display: flex;
        align-items: center;
    }
    
    .profile__tabs a:hover {
        background-color: rgb(217,217,217, .1);
    }
    
    .profile__tab {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
    }
    
    .active {
        color: white;
        font-weight: bold;
    }
    
    .inactive {
        color: rgb(110, 118, 125);
    }
    
    .active-tab {
        height: 5px;
        width: 30%;
        background-color: rgb(29, 155, 240);
        border-radius: 1rem;
    }

}

@media only screen and (max-width: 600px) and (orientation:portrait) {


    .profile__header__main > h3 {
        margin: 0;
    }

    .profile__banner {
        height: 8rem;
        object-fit: fill;
        min-width: 100%;
        max-width: 100%;
        background-color: rgb(47, 51, 54);
        border: none;

    }
    .profile__picture {
        --size: 100px;
        position: absolute;
        max-width: var(--size);
        max-height: var(--size);
        min-width: var(--size);
        min-height: var(--size);
        transform: translateY(calc(8rem - var(--size)/2)) translateX(-80vw);
        border-radius: 50%;
        z-index: 2;
        overflow: visible;
        border: 5px solid black;
        background-color: #bbb;
    }

    #user {
        font-size: 1.5rem;
    }

    .profile__details {
        padding: 0 10px 0 10px;
        width: 90%;
    }

    .profile__follow {
        margin-top: 10px;
    }

    .profile__follow > a {
        text-decoration: none;
        margin-right: 10px;
    }

    .profile__follow > a > span {
        color: white;
    }

    .profile__span {
        color: rgb(101, 119, 134) !important;
        margin-top: .25rem;
        font-size: 14px;
    }

    .profile__tabs {
        display: flex;
        justify-content: space-around;
        border-bottom: 1px solid rgb(95, 108, 116);
        height: 4rem;
        margin-top: 1rem;
    }
    
    .profile__tabs a {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        text-decoration: none;
        align-items: center;
        justify-content: center;
    }
    
    .profile__tabs a > span {
        display: flex;
        align-items: center;
    }
    
    .profile__tabs a:hover {
        background-color: rgb(217,217,217, .1);
    }
    
    .profile__tab {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
    }
    
    .active {
        color: white;
        font-weight: bold;
    }
    
    .inactive {
        color: rgb(110, 118, 125);
    }
    
    .active-tab {
        height: 5px;
        width: 30%;
        background-color: rgb(29, 155, 240);
        border-radius: 1rem;
    }

    .profile__user {
        display: flex;
        align-items: center;
        min-width: 0;
    }
    
    .profile__user__icon {
        height: 3rem;
        margin-right: 1rem;
        border-radius: 5rem;
        max-width: 50px;
        min-width: 50px;
        max-height: 50px;
        min-height: 50px;
    }
    
    .profile__user {
        font-size: large;
    }
    
    .profile__user:hover {
        text-decoration: underline;
    }

    .profile__list {
        list-style: none;
    }
    
    .profile__item {
        margin: 0;
        padding: 0;
        display: flex;
        justify-content: space-between;
    }
    
    .profile__item:hover {
        background-color: #272a2e;
    }
    
    .profile__item > a {
        display: flex;
        padding: 1rem;
        text-decoration: none;
        color: white;
        align-items: center;
        width: 100%;
        justify-content: space-between;
        min-width: 0;
    }

}

