@keyframes slidein {
    from {
        transform: translateY(-100%);
        opacity: 0;
    }

    to {
        transform: translateY(0);
    }
}

.chirp:hover {
    background-color: #131414;
}

.chirp__rechirp>a:hover {
    text-decoration: underline;
}

.chirp__body {
    margin: .5rem 0 0 1rem;
    flex-grow: 1;
}

@media only screen and (min-width: 600px) {
    .chirp {
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        border-bottom: 1px solid rgb(95, 108, 116);
        min-width: 35vw;
        padding: .5rem;
        animation: slidein .5s;
    }

    .chirp__rechirp {
        display: flex;
        margin: 0 0 0 calc(2rem + 50px);
        align-items: center;
    }

    .chirp__rechirp>a {
        text-decoration: none;
        color: rgb(101, 119, 134);
        font-size: 90%;
        margin-left: .5rem;
    }

    .chirp__rechirp>a:hover {
        text-decoration: underline;
    }

    .chirp__main {
        display: flex;
        flex-grow: 1;
        margin-left: 1rem;
        overflow: hidden;
    }

    

    .chirp__icon {
        max-height: 50px;
        min-width: 50px;
        min-height: 50px;
        max-width: 50px;
        background-color: #bbb;
        border-radius: 50%;
        margin-top: .25rem;
        object-fit: cover;
    }

    .chirp__header>span {
        margin: 0 .25rem 0 .5rem;
    }

    .chirp__user {
        text-decoration: none;
        color: white;
        font-weight: bold;
    }

    .chirp__user:hover {
        text-decoration: 1px underline;
    }

    .chirp__timestamp {
        text-decoration: none;
        color: rgb(101, 119, 134);
    }

    .chirp__timestamp:hover {
        text-decoration: underline;
    }

    .chirp__message {
        text-decoration: none;
        color: white;
    }

    .chirp__message>p {
        margin-bottom: .5rem;
        font-weight: normal;
        margin-left: 0;
        overflow-wrap: break-word;
    }

    .chirp__options>section>button {
        background-color: black;
        color: rgb(101, 119, 134);
        border: none;
        padding: .3rem .4rem .3rem .4rem;
        border-radius: 10rem;
        font-weight: bold;
    }


    .chirp__options>section>button:hover {
        background-color: rgba(29, 155, 240, 0.1);
        color: #099bf5;
    }

    .chirp__image {
        display: flex;
        max-width: 100%;
        max-height: 500px;
        border-radius: 1rem;
        scale: 100%;
        margin-top: 1rem;
        margin-bottom: 1rem;
        border: 1px solid gray;
    }

    .chirp__reply {
        text-decoration: none;
        color: rgb(101, 119, 134);
        font-size: smaller;
        margin: .25rem 0 .25rem calc(2rem + 50px);
    }

    .chirp__dropdown {
        position: absolute;
        --width: 50%;
        background-color: grey;
        width: var(--width);
        transform: translateX(calc(var(--width)*1.85*-1)) translateY(-40%);
        box-shadow: 0 0 3px white;
        z-index: 2;
        background-color: black;
        border-radius: .5rem;
    }

    .chirp__dropdown>button {
        display: flex;
        width: 100%;
        align-items: left;
        padding-left: 1rem;
        background: none;
        border: none;
        cursor: pointer;
        padding: 1rem;
    }
    
    .chirp__delete {
        display: flex;
        color: red;
        font-size: 16px;
        align-items: center;
    }
    
    .chirp__delete>span {
        margin-left: 1rem;
    }
    
    .reply {
        width: 34rem;
        min-height: 15rem;
    }
}

